
import { gsap } from "gsap/dist/gsap";
import { useGSAP } from "@gsap/react/dist";
import { ScrollTrigger } from "gsap/dist/ScrollTrigger";
import { Observer } from "gsap/dist/Observer";
// Free plugins
import { TextPlugin } from "gsap/dist/TextPlugin";
import { EaselPlugin } from "gsap/dist/EaselPlugin";
import { MotionPathPlugin } from "gsap/dist/MotionPathPlugin";
import { PixiPlugin } from "gsap/dist/PixiPlugin";
import { ScrollToPlugin } from "gsap/dist/ScrollToPlugin";

// Club plugins (might fail if not available, commenting out strictly paid ones unless I see them)
// I will include the ones user listed but wrapped in try/catch or just imported.
// If build fails, I will remove them.
// Providing a robust implementation using ScrollTrigger.

gsap.registerPlugin(useGSAP, ScrollTrigger, Observer, TextPlugin, ScrollToPlugin);

export function initProjectShowcase() {
    console.log("Initializing Project Showcase");

    // Select all project sections
    const sections = gsap.utils.toArray(".project-section");
    const container = document.querySelector("#project-showcase-container");

    if (!sections.length || !container) return;

    // Pin the container
    gsap.to(sections, {
        yPercent: -100 * (sections.length - 1),
        ease: "none",
        scrollTrigger: {
            trigger: container,
            pin: true,
            scrub: 1,
            snap: 1 / (sections.length - 1),
            // base duration on number of sections
            end: () => "+=" + (window.innerHeight * sections.length)
        }
    });

    // Optional: Animate content within each section
    sections.forEach((section, i) => {
        // Parallax or fade in text?
        // Currently just a simple slide stack.
        // Add specific animations per section if needed.

        const content = section.querySelector(".project-content-wrapper");
        if (content) {
            gsap.from(content, {
                y: 50,
                opacity: 0,
                duration: 1,
                scrollTrigger: {
                    trigger: section,
                    containerAnimation: gsap.getTweensOf(sections)[0], // Link to horizontal/vertical scroll tween if horizontal
                    // For vertical stacked sliding (pinned), the trigger is tricky because they are all in view in the pinned container relative to viewport?
                    // Actually with the logic above (yPercent on sections), they slide up.
                    // ScrollTrigger with scrub on the container handles the movement.
                    start: "top center",
                    toggleActions: "play none none reverse"
                }
            });
        }
    });
}

// Auto-init if DOM is ready
if (document.readyState === "complete" || document.readyState === "interactive") {
    // initProjectShowcase(); // Let main.js or separate script call it?
    // Or just run it.
    initProjectShowcase();
} else {
    document.addEventListener("DOMContentLoaded", initProjectShowcase);
}
